import { inject, Injectable } from '@angular/core';
import { ThemeResponse, ThemesService } from '../../api';
import { BehaviorSubject, map, Observable, tap } from 'rxjs';
import { cloneDeep, merge } from 'lodash';
import {
  isLegacyTheme,
  getThemeDictionary,
  convertLegacyTheme,
} from '../models/theme-dictionary';
import { ThemeEngine } from '../models/theme-engine';
import { isNullOrUndefined } from '../utils';
import {
  ThemeDictionaryEntry,
  ThemeInput,
  ThemeSettings,
} from '../models/theme.model';

@Injectable({
  providedIn: 'root',
})
export class ThemeDataService {
  private readonly themeSubject = new BehaviorSubject<ThemeResponse | null>(
    null,
  );
  private readonly themesService = inject(ThemesService);
  private readonly themeEngine = inject(ThemeEngine);
  theme$ = this.themeSubject.asObservable();

  fetchTheme(themeId: string): Observable<ThemeResponse> {
    return this.themesService.getTheme(themeId).pipe(
      tap((theme) => {
        this.themeSubject.next(theme);
      }),
    );
  }

  setTheme(theme: ThemeInput): ThemeResponse {
    const convertedTheme = this.convertToThemeResponse(theme);
    this.themeSubject.next(convertedTheme);
    return convertedTheme;
  }

  /** This function is called by the APP_INITIALIZER hook before the application is initialized */
  getDomainTheme(domain: string): Observable<ThemeResponse> {
    return this.themesService
      .getThemeForDomain(domain)
      .pipe(map((result) => result.theme));
  }

  private convertToThemeResponse(input: ThemeInput): ThemeResponse {
    const theme = cloneDeep(input) as ThemeResponse;

    if (isLegacyTheme(theme)) {
      const themeDictionary = getThemeDictionary();
      if (isNullOrUndefined(theme.theme) || !(theme.theme in themeDictionary)) {
        throw new Error('Invalid theme key');
      }

      const baseTheme = themeDictionary[theme.theme];
      const mergedTheme = merge(
        cloneDeep(baseTheme),
        theme,
      ) as ThemeDictionaryEntry;

      theme.settings = convertLegacyTheme(mergedTheme) as Record<
        string,
        Record<string, unknown>
      >;
    }

    if (theme.settings) {
      theme.variables = {
        ...theme.variables,
        ...Object.fromEntries(
          Object.entries(
            this.themeEngine.settingsToVariables(
              theme.settings as ThemeSettings,
            ),
          ).map(([key, value]) => [
            key,
            Array.isArray(value) ? value.join(',') : value,
          ]),
        ),
      };
    }

    return theme;
  }
}
