export function isLegacyTheme(theme: ThemeResponse): boolean {
  const themeDictionary = getThemeDictionary();
  const base = isDefined(theme.theme)
    ? themeDictionary[theme.theme]
    : undefined;
  if (isDefined(base)) {
    const legacyKeys = base.variableSettings.map((x) => x.key);
    return (
      theme.theme === 'nightrider' ||
      intersection(legacyKeys, Object.keys(theme.settings || {})).length > 0
    );
  }
  return false;
}

export function convertLegacyTheme(
  theme: ThemeDictionaryEntry,
): Record<string, ThemeValue> {
  const result: Record<string, ThemeValue> = {};

  if (theme['theme'] === 'nightrider') {
    set(result, 'main.background.color', '#161616');
    set(result, 'content.background.color', '#252526');
    set(result, 'header.background.color', '#333333');
    set(result, 'footer.background.color', '#4a4a4a');
    set(result, 'table.header.background.color', '#2e2e2e');
  }
  set(
    result,
    'navigation.active.style',
    isDefined(theme.settings?.['navigationActiveState']) ||
      (theme.theme === 'nightrider' ? 'background' : 'left-border'),
  );

  const defaultSettings =
    isDefined(theme.theme) && theme.theme in THEME_DEFAULT_SETTINGS
      ? THEME_DEFAULT_SETTINGS[theme.theme]
      : undefined;
  theme.variableSettings?.forEach((item) => {
    const value = getComputedColor(
      item,
      isDefined(theme['settings']) ? (theme['settings'] as ThemeSetting) : {},
      defaultSettings,
    );
    switch (item.key) {
      case 'navigationColor_background':
        set(result, 'navigation.background.color', value);
        break;
      case 'navigationColor_foreground':
        set(result, 'navigation.font.color', value);
        break;
      case 'navigationColor_border':
        set(result, 'navigation.border.color', value);
        break;
      case 'navigationColor_accent':
        break;
      case 'navigationColor_accentInvert':
        break;
      case 'headerColor_background':
        set(result, 'header.background.color', value);
        if (theme['theme'] === 'nightrider') {
          set(result, 'footer.background.color', value);
        }
        break;
      case 'headerColor_foreground':
        set(result, 'header.font.color', value);
        if (theme['theme'] === 'nightrider') {
          set(result, 'footer.font.color', value);
        }
        break;
      case 'headerColor_border':
        set(result, 'header.border.color', value);
        break;
      case 'headerColor_accent':
        break;
      case 'headerColor_accentInvert':
        break;
      case 'headerColorActive_background':
        set(result, 'tab.active.background.color', value);
        break;
      case 'headerColorActive_foreground':
        set(result, 'tab.active.font.color', value);
        break;
      case 'headerColorActive_border':
        set(result, 'tab.active.border.color', value);
        break;
      case 'primaryColor_main':
        set(result, 'color.primary.color', value);
        break;
      case 'primaryColor_invert':
        set(result, 'color.primary.contrast', value);
        break;
      default:
        break;
    }
  });
  return result;
}

function getComputedColor(
  item: ThemeVariable,
  settings: ThemeSetting,
  defaultSettings?: ThemeSetting,
): string {
  if (isNullOrUndefined(item.relatedColor)) {
    return settings[item.key] as string;
  }

  const relatedColor = Color(settings[item.relatedColor] as string);
  const textOnDark = Color(settings['textColor_default'] as string).isDark()
    ? settings['textColor_invert']
    : settings['textColor_default'];
  const isDefault =
    settings[item.relatedColor] === defaultSettings?.[item.relatedColor];

  switch (item.type) {
    case 'contrast':
      return isDefault
        ? (defaultSettings[item.key] as string)
        : relatedColor.isDark()
          ? (textOnDark as string)
          : relatedColor.darken(0.8).string();
    case 'match':
      return isDefault
        ? (defaultSettings[item.key] as string)
        : (settings[item.relatedColor] as string);
    case 'border':
    case 'accent':
      return isDefault
        ? (defaultSettings[item.key] as string)
        : relatedColor.isDark()
          ? relatedColor.lighten(0.1).string()
          : relatedColor.darken(0.1).string();
    default:
      return settings[item.key] as string;
  }
}

const getDefaultThemeForm = (
  i18n: TranslateService,
  baseTheme: ThemeDictionaryEntry,
  fields?: ThemeField[],
): ThemeForm => {
  const translate = (value: string): string =>
    isDefined(i18n) ? i18n.instant('SHARED.THEME.' + value) : value;

  const resetAction: ThemeAction = {
    icon: 'undo',
    name: i18n.instant('ADMIN.EDITORS.CLIENT.CUSTOMIZE.RESET_COLOR'),
    click: (
      _action: string,
      field: ThemeField,
      values: Record<string, unknown>,
    ) => {
      if (field.type === 'swatches' && isDefined(field.values)) {
        field.values.forEach((kv) => {
          values[kv.key] = baseTheme.settings?.[kv.key];
        });
      } else {
        values[field.id] = baseTheme.settings?.[field.id];
      }
      return null;
    },
  };

  return {
    groups: [
      {
        fields: [
          {
            type: 'color',
            id: 'primaryColor_main',
            title: translate('PRIMARY_COLOR'),
            required: false,
            actions: [resetAction],
          },
          {
            type: 'swatches',
            id: 'navigationColor',
            title: translate('NAVIGATION_COLOR'),
            required: false,
            actions: [resetAction],
            values: [
              {
                key: 'navigationColor_background',
                value: translate('BACKGROUND'),
              },
              {
                key: 'navigationColor_foreground',
                value: translate('FOREGROUND'),
              },
            ],
          },
          {
            type: 'combo',
            id: 'navigationActiveState',
            title: translate('NAVIGATION_ACTIVE_STATE'),
            actions: [resetAction],
            values: [
              { key: 'background', value: translate('BACKGROUND') },
              { key: 'left-border', value: translate('LEFT_BORDER') },
              { key: 'right-border', value: translate('RIGHT_BORDER') },
            ],
            required: false,
          },
          {
            type: 'swatches',
            id: 'headerColor',
            title: translate('HEADER_COLOR'),
            required: false,
            actions: [resetAction],
            values: [
              { key: 'headerColor_background', value: translate('BACKGROUND') },
              { key: 'headerColor_foreground', value: translate('FOREGROUND') },
            ],
          },
          {
            type: 'swatches',
            id: 'headerColorActive',
            title: translate('HEADER_ACTIVE_COLOR'),
            required: false,
            actions: [resetAction],
            values: [
              {
                key: 'headerColorActive_background',
                value: translate('BACKGROUND'),
              },
              {
                key: 'headerColorActive_foreground',
                value: translate('FOREGROUND'),
              },
            ],
          },
          ...(fields || []),
        ],
      },
    ],
  };
};

/**
 * default configs for themes
 *
 * WARNING: When changing these settings make sure the theme's default variables inside its scss config file gets updated too.
 * I know this is duplication, there is a future dude implementation of theming that will try to fix this
 */
export const getThemeDictionary = (): Record<string, ThemeDictionaryEntry> => {
  const value: Record<string, ThemeDictionaryEntry> = {
    default: {
      id: 'default',
      owner: null,
      entity: null,
      theme: 'default',
      name: 'Default Light',
      variableSettings: [
        ...THEME_VARIABLES,
        {
          key: 'primaryColor_main',
          type: 'color',
          variables: ['--color-primary', '--color-text-link'],
        },
        {
          key: 'primaryColor_invert',
          type: 'contrast',
          relatedColor: 'primaryColor_main',
          variables: ['--color-primary-contrast'],
        },
      ],
      settings: THEME_DEFAULT_SETTINGS['default'],
      customStyles: '',
      logoPath: '',
      getSettingsForm: (i18n: TranslateService, fields?: ThemeField[]) =>
        getDefaultThemeForm(i18n, value['default'], fields),
    },
    nightrider: {
      id: 'nightrider',
      owner: null,
      entity: null,
      theme: 'nightrider',
      name: 'Default Dark',
      variableSettings: [
        ...THEME_VARIABLES,
        {
          key: 'primaryColor_main',
          type: 'color',
          variables: [
            '--color-primary',
            '--color-text-link',
            '--color-bg-list-active',
          ],
        },
        {
          key: 'primaryColor_invert',
          type: 'contrast',
          relatedColor: 'primaryColor_main',
          variables: [
            '--color-primary-contrast',
            '--color-bg-list-active-contrast',
          ],
        },
      ],
      settings: THEME_DEFAULT_SETTINGS['nightrider'],
      customStyles: '',
      logoPath: '',
      getSettingsForm: (
        i18n: TranslateService,
        fields: ThemeField[] | undefined,
      ) => getDefaultThemeForm(i18n, value['nightrider'], fields),
    },
  };
  return cloneDeep(value);
};
