import { merge } from 'lodash';
import { THEME_DEFAULTS } from './theme-defaults';
import { ThemeColor } from './theme-color';
import {
  hasProperty,
  isDefined,
  isEmpty,
  isNullOrUndefined,
  isObject,
  isString,
} from '../utils';
import { ThemeVariables, FormulaCache, ThemeSettings } from './theme.model';
import { Injectable } from '@angular/core';

const colorPrefix = ['#', 'rgb(', 'rgba(', 'hsl(', 'hwb('];
const FALLBACK_COLOR = '#000000';

export function attempt(func: () => void) {
  try {
    return func();
  } catch (err) {
    console.error(err);
    return FALLBACK_COLOR;
  }
}

export function getvar(input: string | ThemeColor): ThemeColor | string {
  if (input instanceof ThemeColor) {
    return input;
  }

  if (colorPrefix.some((prefix) => input.startsWith(prefix))) {
    return new ThemeColor(input);
  }

  return input;
}

@Injectable({
  providedIn: 'root',
})
export class ThemeEngine {
  public variables: ThemeVariables = {};
  private formulaCache: FormulaCache = {};
  private readonly ALLOWED_OPERATORS = ['+', '-', '*', '/', '(', ')', ',', '.'];
  private readonly ALLOWED_FUNCTIONS = [
    'var',
    'rgb',
    'rgba',
    'hsl',
    'hwb',
    'family',
    'getvar',
    'inverse',
    'lighten',
    'weight',
    'function',
    'return',
    'color',
    'transform',
    'size',
    'darken',
    'width',
    'shadow',
    'pack',
    'style',
    'fade',
    'contrast',
    'position',
    'radius',
    'palette',
  ];

  private sanitizeExpression(expression: string): string {
    // Remove all whitespace
    let sanitized = expression.replace(/\s+/g, '');

    // Only allow specific characters and functions
    sanitized = sanitized
      .split('')
      .filter(
        (char) =>
          this.ALLOWED_OPERATORS.includes(char) || /[0-9a-zA-Z]/.test(char),
      )
      .join('');

    return sanitized;
  }

  settingsToVariables(settings: ThemeSettings): ThemeVariables {
    return this.resolveVariables(
      this.themeSettingsToVariables(
        this.themeSettingsMergedWithDefault(settings),
      ),
    );
  }

  resolveFormula(formula: string): string {
    if (isEmpty(this.variables)) {
      return FALLBACK_COLOR;
    }

    if (!hasProperty(this.formulaCache, formula)) {
      this.resolveVariables({
        ...this.variables,
        '--single-formula': formula,
      });
    }

    const result = hasProperty(this.formulaCache, formula)
      ? this.formulaCache[formula]
      : FALLBACK_COLOR;

    return result;
  }

  resolveVariables(variables: ThemeVariables): ThemeVariables {
    this.variables = variables; // unpack needs access to the full current set

    const unpack = (valStr: string, depth = 0): string => {
      if (isNullOrUndefined(valStr)) {
        return `getvar(${JSON.stringify(FALLBACK_COLOR)})`;
      }

      valStr = String(valStr);

      if (depth >= 10) {
        console.warn(`⚠️ Max recursion depth reached for: ${valStr}`);
        return `getvar(${JSON.stringify(FALLBACK_COLOR)})`;
      }

      const varRegex = /var\s*\((--[^)]+)\)/g;

      if (varRegex.test(valStr)) {
        varRegex.lastIndex = 0;
        const replacedVarString = valStr.replace(varRegex, (match, varName) => {
          if (!hasProperty(this.variables, varName)) {
            return `getvar(${JSON.stringify(FALLBACK_COLOR)})`;
          }

          const varActualValue = this.variables[varName];
          if (isNullOrUndefined(varActualValue)) {
            console.warn(`❌ Variable ${varName} is null/undefined`);
            return `getvar(${JSON.stringify(FALLBACK_COLOR)})`;
          }

          return unpack(String(varActualValue), depth + 1);
        });

        return replacedVarString;
      }

      // Base case for recursion: valStr is a terminal value (no var() in it)
      if (colorPrefix.some((prefix) => valStr.startsWith(prefix))) {
        // It's a color string like "#fff", "rgb(...)"
        return `getvar(${JSON.stringify(valStr)})`;
      } else if (
        !isNaN(parseFloat(valStr)) &&
        isFinite(valStr as unknown as number) &&
        valStr.trim() !== ''
      ) {
        // It's a number string like "0.5", "10". Return as is to be a JS number.
        return valStr;
      } else {
        // It's another string literal like "solid", "10px" (that isn't a plain number).
        // Return as a JS string literal.
        return JSON.stringify(valStr);
      }
    };

    let evaled: ThemeVariables;
    // Directly use the original variables to generate expressions
    const code = `{ ${Object.entries(variables)
      .filter(([key]) => isString(key)) // Ensure key is a string
      .map(([key, originalValue]) => {
        // Ensure the key is properly escaped for use as a JS string literal
        const escapedKey = JSON.stringify(key);
        // unpack returns the full expression string for the originalValue
        const expressionToEvaluate = unpack(originalValue as string);

        return `${escapedKey}: (function() { 
                   try { 
                     return ${expressionToEvaluate}; // Evaluate the fully formed expression
                   } catch (e) { 
                     console.error('Error evaluating variable ' + ${escapedKey} + ' (expression: "${expressionToEvaluate}"):', e, '. Falling back to default.'); 
                     return getvar(${JSON.stringify(FALLBACK_COLOR)}); 
                   } 
                 })()`;
      })
      .join(',')} }`;

    try {
      evaled = this.safeEval(code, { getvar });
    } catch (err) {
      console.error(
        'Critical evaluation failure during safeEval:',
        err,
        'Code:',
        code,
      );
      evaled = Object.keys(variables).reduce((acc, key) => {
        acc[key] = FALLBACK_COLOR;
        return acc;
      }, {} as ThemeVariables);
    }

    // Populate formulaCache and the final result object
    return Object.entries(evaled).reduce((obj: ThemeVariables, [key, val]) => {
      if (isDefined(val)) {
        const str = this.isThemeColor(val) ? val.hex() : String(val);
        const originalFormula = variables[key]; // The original string value for this key
        if (isString(originalFormula)) {
          this.formulaCache[originalFormula] = str;
        }
        // Special handling for --single-formula if it was the key used for dynamic resolution
        if (
          key === '--single-formula' &&
          isString(variables['--single-formula'])
        ) {
          this.formulaCache[variables['--single-formula']] = str;
        }
        obj[key] = str;
      } else {
        obj[key] = FALLBACK_COLOR;
        const originalFormula = variables[key];
        if (isString(originalFormula)) {
          this.formulaCache[originalFormula] = FALLBACK_COLOR;
        }
      }
      return obj;
    }, {});
  }

  private isThemeColor(value: unknown): value is ThemeColor {
    return (
      isObject(value) &&
      hasProperty(value, 'hex') &&
      typeof value['hex'] === 'function'
    );
  }

  themeSettingsToVariables(
    settings: ThemeSettings,
    separator = '-',
  ): ThemeVariables {
    const flat = (obj: Record<string, unknown>): ThemeVariables =>
      Object.entries(isDefined(obj) ? obj : {}).reduce(
        (acc: ThemeVariables, [key, value]) => {
          if (!isObject(value)) {
            return {
              ...acc,
              [key]: String(value),
            };
          }

          const flattenedChild = flat(value);
          return {
            ...acc,
            ...Object.entries(flattenedChild).reduce(
              (childAcc: ThemeVariables, [childKey, childValue]) => ({
                ...childAcc,
                [`${key}${separator}${childKey}`]: childValue,
              }),
              {},
            ),
          };
        },
        {},
      );

    const flattened = flat(settings);
    return Object.entries(flattened).reduce(
      (obj: ThemeVariables, [key, value]) => {
        if (isDefined(value)) {
          obj[`${separator}${separator}${key}`] = value;
        }
        return obj;
      },
      {},
    );
  }

  themeSettingsMergedWithDefault(settings: ThemeSettings) {
    return merge({}, THEME_DEFAULTS, settings);
  }

  safeEval(
    expression: string,
    context: Record<
      string,
      ThemeColor | ((input: string | ThemeColor) => ThemeColor | string)
    > = {},
  ): ThemeVariables {
    const contextKeys = Object.keys(context);
    const contextValues = Object.values(context);
    const func = new Function(...contextKeys, `return ${expression};`);
    return func(...contextValues);
  }
}
